<script>
import autophp from '@/plugins/islamic_auto';
let sdb = new autophp();
import firebase from 'firebase'
export default{
    data(){
        return{
            show:false,
            cordova: false,
            ready:true,
            users:false,
            links:[
                {
                    link:'/app/islamic',
                    nama:'Dashboard'
                },
                {
                    link:'/app/islamic/listsurah',
