                {
                    link:'/app/islamic/bacaansholat',
                    nama:'Bacaan Sholat'
                },
                // {
                //     link:'/app/islamic/jadwalsholat',
                //     nama:'Jadwal Sholat'
                // },
                {
                    link:'/app/islamic/asmaulhusna',
                    nama:'Asmaul Husna'
                },
                {
                    link:'/app/islamic/tahlil',
                    nama:'Tahlil'
                },
                {
                    link:'/app/islamic/dzikircounter',
                    nama:'Dzikir Counter'
                },
                // {
                //     link:'/app/islamic/sholawatnabi',
                //     nama:'Sholawat Nabi'
                // },
                 {
                    link:'/app/islamic/dzikirpagi',
                    nama:'Dzikir Pagi'
                },
                 {
                    link:'/app/islamic/dzikirpetang',
                    nama:'Dzikir Petang'
                },
                {
                    link:'/app/islamic/setting',
                    nama:'Setting'
                },
                //  {
                //     link:'/test',
                //     nama:'test'
                // },
            ]
        }
    },
    methods: {
        setting(){
            this.$router.push('/app/islamic/setting')
            // this.$store.dispatch('logout')
            // firebase.auth().signOut().then(() => {
            //     alert('logout')
            //     console.log('logout berhasil')
            //     }).catch((error) => {
            // });
        },
        onswiperight(){
            this.show=true;
        },
        googleAuth() {
            let that=this;
            const provider = new firebase.auth.GoogleAuthProvider();
            firebase
            .auth()
            .signInWithPopup(provider)
            .then(data => {
            let user = {
                uid: data.user.uid,
                displayName: data.user.displayName,
                email: data.user.email,
                emailVerified: data.user.emailVerified,
                gambar:data.user.photoURL
            };
            firebase.auth().currentUser.getIdToken(/* forceRefresh */ true).then(function(idToken) {
                localStorage.setItem("auth._token.local", 'bearer '+idToken);
                localStorage.setItem("auth.local", 'bearer '+idToken);
                }).catch(function(error) {
            });
            this.$store.commit("changeUsers", user);
            // console.log('user',user)
            localStorage.setItem("users", JSON.stringify(user));
            let db = firebase.firestore();
            db.collection(`users`).doc(data.user.uid).set(user,{merge:true}).then(res=>{
                console.log('berhasil')
                that.$router.push('/')
            }).catch(err=>{
                console.log(err.message)
            })
            })
            .catch(err => {
            alert("Oops. " + err.message);
            });
        },
         googleCordova(){
      let that = this;
      window.plugins.googleplus.login(
        {
          webClientId:
            "272608669461-h28085mhjebne2cvtgacp08fibalj1a7.apps.googleusercontent.com",
          offline: true
        },
        function(obj) {
          firebase
            .auth()
            .signInWithCredential(
              firebase.auth.GoogleAuthProvider.credential(obj.idToken)
            )
            .then(data => {
              let user = {
                uid: data.user.uid,
                displayName: data.user.displayName,
                email: data.user.email,
                emailVerified: data.user.emailVerified,
                gambar:data.user.photoURL
            };
               firebase.auth().currentUser.getIdToken(/* forceRefresh */ true).then(function(idToken) {
                localStorage.setItem("auth._token.local", 'bearer '+idToken);
                localStorage.setItem("auth.local", 'bearer '+idToken);
                }).catch(function(error) {
            });
              this.$store.commit("changeUsers", user);
            // console.log('user',user)
            localStorage.setItem("users", JSON.stringify(user));
                let db = firebase.firestore();
                db.collection(`users`).doc(data.user.uid).set(user,{merge:true}).then(res=>{
                    console.log('berhasil')
                    that.$router.push('/')
                }).catch(err=>{
                    console.log(err.message)
                })
            })
        },
        function(msg) {
          alert("error: " + msg);
        }
      );
    },
    },
    mounted() {
        let that=this;
        // #CORDOVA
        if (typeof cordova === "object") {
            document.addEventListener("deviceready", function() {
                that.cordova=true;
            });
        }
        firebase.auth().onAuthStateChanged((user) => {
            if (user) {
                let users = {
                    uid: user.uid,
                    displayName: user.displayName,
                    email: user.email,
                    emailVerified: user.emailVerified,
                    gambar:user.photoURL
                };
                firebase.auth().currentUser.getIdToken(/* forceRefresh */ true).then(function(idToken) {
                    localStorage.setItem("auth._token.local", 'bearer '+idToken);
                    localStorage.setItem("auth.local", 'bearer '+idToken);
                     that.ready=true;
                     that.$forceUpdate();
                    }).catch(function(error) {
                });
                this.$store.commit("changeUsers", users);
                localStorage.setItem("users", JSON.stringify(users));
            } else {
                that.$forceUpdate();
                // console.log('user sesion tidak ada')
            }
        });
    },
      metaInfo: {
        title: `Baca Al Qur'an`,
        link: [
         { rel: 'preconnect', href: 'https://fonts.googleapis.com' },
         { rel: 'preconnect', href: 'https://fonts.gstatic.com' },
